
// Custom mixins for project galleries


/* -------- For Image Sizes --------- */

@mixin imageSmaller {
	max-height: 550px;
	width: auto;
}

@mixin imageMobile {
	max-height: 440px;
	width: auto;
	margin: 0 auto;
}

/* ----------------------------------- */


/* -------- For Image Position ------- */

@mixin centerImg {
	justify-self: center;
}

@mixin bottom-left {
	align-self: end;
}

@mixin top-right {
	justify-self: end;
}

@mixin bottom-right {
	justify-self: end;
	align-self: end;
}

/* ----------------------------------- */







/* -------- UnderScores Mixins ------- */

// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

// Clearfix
@mixin clearfix() {
	content: "";
	display: table;
	table-layout: fixed;
}

// Clear after (not all clearfix need this also)
@mixin clearfix-after() {
	clear: both;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
	width: map-get( $columns, $numberColumns ) - ( ( $columns__margin * ( $numberColumns - 1 ) ) / $numberColumns );
}

/* ----------------------------------- */